                <div class="beans-cof1"></div>
                <div class="beans-cof2"></div>
            </div> -->


            <!-- <div class="parent-beans2">
                <div class="beans-cof1"></div>
                <div class="beans-cof2"></div>
            </div> -->

            <div class="circle-landing" :style="{
                left: `${x}px`,
                top: `${y}px`
            }" />

            <div class="type-writing">
                <div class="text">I'm,
                    <span id="wrod-writing">{{ typeValue }}</span>
                    <span :class="{ 'typing': typeStatus }"></span>
                </div>
            </div>




            <!-- {{ x }} {{ y }} -->
        </div>

    </div>
</template>


<script setup>
import { useMouse } from '@vueuse/core';
// import { onMounted } from "vue";


const { x, y } = useMouse();

// let text = document.querySelector(".type-writing .text span");

// let out = "ziad Amr";



// onMounted(() => {
