</script>

<script>


export default {
    name: 'LandingBack',
    data() {
        return {
            typeValue: '',
            typeStatus: false,
            typeArray: ["Freelancer", "Devoloper"],
            typingSpeed: 200,
            erasingSpeed: 100,
            newTextDelay: 2000,
            typeArrayIndex: 0,
            charIndex: 0
        }
    },
    methods: {
        RunWriter() {

            // let words = ["a journey", "Devoloper"];

            let word1 = this.words[0];
            let word2 = this.words[1];

            window.onload = () => {
                this.typeValue = "";
            }

            let i = 0;

            this.typeValue = "";
            const typeWriting = setInterval(() => {

                this.typeValue += word1[i];

                i++;

                if (i > word1.length - 1) {
                    clearInterval(typeWriting);
                }

            }, 150)

            let arrWord1 = Array.from(word1);

            setTimeout(() => {

                let deleteWord1 = setInterval(() => {

                    arrWord1.splice(arrWord1.length - 1, 1);
                    this.typeValue = arrWord1.join("");

                    if (arrWord1.length === 0) {
                        clearInterval(deleteWord1)
                    }

                }, 150)

            }, 1500)

            // [ 2 ]

            let r = 0;

            this.typeValue = "";

            setTimeout(() => {

                const typeWriting2 = setInterval(() => {

                    this.typeValue += word2[r];

                    r++;

                    if (r > word2.length - 1) {
                        clearInterval(typeWriting2);
                    }
                }, 150)


            }, 1500 * 2)

            let arrWord2 = Array.from(word2);

            setTimeout(() => {

                let deleteWord2 = setInterval(() => {

                    arrWord2.splice(arrWord2.length - 1, 1);
                    this.typeValue = arrWord2.join("");

                    if (arrWord2.length === 0) {
                        clearInterval(deleteWord2)
                    }

                }, 150)

            }, 1500 * 3)


        },
        repeatWriter() {
            this.RunWriter();

            setInterval(() => {
                this.RunWriter();
            }, 1500 * 4)
        },
        typeText() {
            if (this.charIndex < this.typeArray[this.typeArrayIndex].length) {
                if (!this.typeStatus)
                    this.typeStatus = true;

                this.typeValue += this.typeArray[this.typeArrayIndex].charAt(this.charIndex);
                this.charIndex += 1;

                setTimeout(this.typeText, this.typingSpeed);
            }
            else {
                this.typeStatus = false;
                setTimeout(this.eraseText, this.newTextDelay);
            }
        },
        eraseText() {
            if (this.charIndex > 0) {
                if (!this.typeStatus)
                    this.typeStatus = true;

                this.typeValue = this.typeArray[this.typeArrayIndex].substring(0, this.charIndex - 1);
                this.charIndex -= 1;
                setTimeout(this.eraseText, this.erasingSpeed);
            }
            else {
                this.typeStatus = false;
                this.typeArrayIndex += 1;
                if (this.typeArrayIndex >= this.typeArray.length)
                    this.typeArrayIndex = 0;

                setTimeout(this.typeText, this.typingSpeed + 1000);
            }
        }
    },
    created() {
        setTimeout(this.typeText, this.newTextDelay + 200);
    }
}
</script>


