<template>
    <div class="header" id="header">

        <div class="container">
            <div class="logo">
                <!-- <h2>Coffee</h2> -->
                <img src="../assets/logo.png" width="70" height="70" />
            </div>

            <nav>
                <router-link to="/" class="link" id="home-link">Home</router-link>
                <router-link to="/about" class="link" id="about-link">About</router-link>
                <router-link to="/menu" class="link" id="menu-link">Menu</router-link>

                <div class="shop">
                    <router-link to="/shop" class="shop link" id="shop-link">Shop</router-link>
                    <div class="product">
                        <ul class="ls-style">
                            <li v-for="(product, index) in products" :key="index">{{ product }}</li>
                        </ul>
                    </div>
                </div>


                <router-link to="/contact" class="link" id="contact-link">Contact</router-link>
            </nav>

            <div class="action-l-s">
                <div class="car-shop">
                    <router-link to="/carshop">
                        <FontAwesome :icon="['fas', 'cart-shopping']" class="icon-car-shop" />
                    </router-link>

                </div>

                <div class="sign-in">
                    <!-- <FontAwesome :icon="['fas', 'right-to-bracket']" class="login"/> -->
                    <router-link to="/signin" calss="txt-dec login">
                        <FontAwesome :icon="['fas', 'right-to-bracket']" class="icon-login" />
                    </router-link>
                    <!-- <button id="car">car</button> -->
                </div>

                <div class="menu" id="menu" @click="activeMenu">
                    <div class="icon-menu">
                        <div class="col-100P"></div>
                        <div class="col-100P"></div>
                        <div class="col-100P"></div>
                    </div>
                </div>

                <div class="links" id="links">

                    <div><router-link to="/" @click="hiddenMenu">Home</router-link></div>
                    <div><router-link to="/about" @click="hiddenMenu">About</router-link></div>
                    <div><router-link to="/menu" @click="hiddenMenu">Menu</router-link></div>
                    <div><router-link to="/shop" class="shop" @click="hiddenMenu">Shop</router-link></div>
                    <div><router-link to="/contact" @click="hiddenMenu">Contact</router-link></div>

                    <div class="name-devoloper">@Ziad Amr, I'm Devoloper</div>
                </div>

                <div class="overlay" id="overlay"></div>
            </div>

        </div>

    </div>
    <router-view class="router-view" />
</template>

<script>

const products = ["Hot Coffe", "Iced Coffe", "Frappes", "Refreshers"];

export default {
    name: 'NavBar',

    async setup() {
        // let headerQ = document.getElementById("header");

        const promise = new Promise((resolve) => {

            setTimeout(() => {
                resolve("Success")
            }, 2000)

        })

        const NavBar = await promise;


        return {
            NavBar,
        }
    },
    data() {
        return {
            products,
            isActive: false
        }
    },
    methods: {
        showNow() {
            let HeaderF = document.getElementById("header");


            window.onscroll = () => {

                if (window.scrollY >= 70) {
                    HeaderF.classList.add("header");
                    HeaderF.classList.add("shadow");
                }

                if (window.scrollY < 70) {
                    HeaderF.classList.remove("shadow");
                }
            }
        },
        activeMenu() {
            let links = document.getElementById("links");
            // active-menu
            let menu = document.getElementById("menu");
            let overlay = document.getElementById("overlay");

            links.classList.toggle("active");

            menu.classList.toggle("active-menu");

            overlay.classList.toggle("active-overlay");

            document.body.classList.toggle("stop-scroll");

        },
        hiddenSideBar() {
            let menu1 = document.getElementById("menu");
            let links1 = document.getElementById("links");
            let overlay1 = document.getElementById("overlay");

            // console.log("ziad Run");

            function changeMenu(x) {
                if (x.matches) {
                    menu1.classList.remove("hidden");
                    links1.classList.remove("hidden");
                    overlay1.classList.remove("hidden");

                    if (menu1.classList.contains("active-menu")) {
                        document.body.classList.add("stop-scroll");
                    }

                } else {
                    menu1.classList.add("hidden");

                    if (menu1.classList.contains("hidden")) {
                        links1.classList.add("hidden");
                        overlay1.classList.add("hidden");
                        document.body.classList.remove("stop-scroll");
                    }

                }
            }

            let media_smobile = window.matchMedia("(max-width: 600px)");

            media_smobile.addEventListener("change", function () {
                changeMenu(media_smobile);
            });
        },
        hiddenMenu() {
            let menu2 = document.getElementById("menu");

            menu2.click();
        },
        activeLinks() {


            let delActive = document.querySelectorAll("nav a");

            delActive.forEach((e) => {
                e.classList.remove("active-links");
            })
            localStorage.setItem("links", "active-links");

            document.addEventListener("click", (e) => {
                if (e.target.classList.contains("link")) {
                    if (localStorage.getItem("links")) {
                        e.target.classList.add(localStorage.getItem("links"));
                    }
                    // e.target.classList.add(localStorage.getItem("links"));
                }
            })

        },
        delteActive() {
            let delActive = document.querySelectorAll("nav a");

            delActive.forEach((e) => {
                e.classList.remove("active-links");
            })

        },

    },

    mounted() {

        return this.showNow(),
            this.hiddenSideBar();
    },

}
</script>



